/* Third party */
@import "../../bower_components/normalize.css/normalize.css";
/* Custom */
/***************************************************/
/* Set custom or change third-party Less variables */
/***************************************************/
/* Third party */
/*
LESS variables are information about icon's compiled state, stored under its original file name

.icon-home {
  width: @icon-home-width;
}

The large array-like variables contain all information about a single icon
@icon-home: x y offset_x offset_y width height total_width total_height image_path name;

At the bottom of this section, we provide information about the spritesheet itself
@spritesheet: width height image @spritesheet-sprites;
*/
/*
These "retina group" variables are mappings for the naming and pairing of normal and retina sprites.

The list formatted variables are intended for mixins like `retina-sprite` and `retina-sprites`.
*/
/*
The provided classes are intended to be used with the array-like variables

.icon-home {
  .sprite-width(@icon-home);
}
.icon-email {
  .sprite(@icon-email);
}
*/
/*
The `retina-sprite` class sets up rules and a media query for a sprite/retina sprite.
  It should be used with a "retina group" variable.

The media query is from CSS Tricks: https://css-tricks.com/snippets/css/retina-display-media-query/

@icon-home-group: 'icon-home' @icon-home @icon-home-2x;

.icon-home {
  .retina-sprite(@icon-home-group);
}
*/
/*
The `.sprites` mixin generates identical output to the CSS template
  but can be overridden inside of LESS

This must be run when you have at least 2 sprites.
  If run with a single sprite, then there will be reference errors.

.sprites(@spritesheet-sprites);
*/
/*
The `.retina-sprites` mixin generates a CSS rule and media query for retina groups
  This yields the same output as CSS retina template but can be overridden in LESS

.retina-sprites(@retina-groups);
*/
.png-sprite:before {
  content: '';
  display: inline-block;
}
.svg-sprite:before {
  content: '';
  display: inline-block;
  width: 32px!important;
  height: 32px!important;
  background-size: contain!important;
}
.roboto-condensed {
  font-family: 'Roboto Condensed', sans-serif;
}
div,
article,
footer,
main {
  box-sizing: border-box;
  font-family: 'Roboto Condensed', sans-serif;
}
.container {
  width: 1920px;
  padding-left: 20px;
  padding-right: 20px;
  height: 1080px;
  margin: 0 auto;
  clear: both;
  position: relative;
}
.container:before {
  content: '';
  display: table;
}
.card {
  width: 376px;
  height: 1080px;
  float: left;
  padding-top: 221px;
  background-color: #ffffff;
  cursor: pointer;
}
.card--flip-back {
  background-color: #5a5a5a;
}
.card__header {
  width: 345px;
  height: 123px;
  background-color: #024b9d;
  margin: 0 auto;
  text-align: center;
  display: table;
  padding: 0 15px;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.3);
  z-index: 10;
  position: absolute;
  top: 48px;
  left: calc(50% - 172px);
}
.card__header--2 {
  opacity: .9;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.4);
}
.card__header--3 {
  opacity: .8;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.5);
}
.card__header--4 {
  opacity: .7;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.6);
}
.card__header--5 {
  opacity: .6;
  box-shadow: 0 50px 30px -30px rgba(0, 0, 0, 0.7);
}
.card__title {
  display: table-cell;
  vertical-align: middle;
  font-size: 28px;
  font-weight: bold;
  line-height: 34px;
  text-transform: uppercase;
  margin: 0;
  color: #fff;
}
.card__img {
  height: 1080px;
  position: absolute;
  top: 0;
}
.card__list {
  list-style-type: none;
  padding-left: 40px;
  margin: 0;
}
.card__list-item {
  padding: 0;
  padding-bottom: 25px;
}
.card__list-link {
  text-transform: uppercase;
  font-size: 18px;
  color: #fff;
  letter-spacing: .9px;
  text-decoration: none;
  transition: .5s;
}
.card__list-link--current {
  color: #3399ff;
}
.card__list-link:hover {
  color: #3399ff;
}
.footer-wrapper {
  width: 1920px;
  position: relative;
  bottom: 228px;
  margin: 0 auto;
  padding-top: 18px;
  overflow: hidden;
  left: -20px;
}
.footer {
  opacity: .9;
  width: 1920px;
  height: 210px;
  background-color: #024b9d;
  box-shadow: 0 0px 18px 0 #000;
  padding-top: 50px;
}
.footer__main {
  clear: both;
  text-align: center;
  position: relative;
  margin-bottom: 40px;
}
.footer__contacts {
  font-size: 29px;
  font-weight: bold;
  color: #fff;
  display: inline-block;
  position: absolute;
  left: 98px;
  bottom: 0;
}
.footer__contacts--email {
  right: 113px;
  left: inherit;
}
.footer__logo {
  display: inline-block;
}
.footer__copyright {
  text-align: center;
  opacity: .25;
  color: #fff;
}
.flip {
  perspective: 800px;
  position: relative;
  width: 376px;
  height: 1080px;
  float: left;
}
.flip__front {
  transform-style: preserve-3d;
  backface-visibility: hidden;
  position: absolute;
  transition: 0.8s;
}
.flip__back {
  transform-style: preserve-3d;
  backface-visibility: hidden;
  position: absolute;
  transition: 1s;
  transform: rotateY(180deg);
}
.flip:hover .flip__front {
  transform: rotateY(180deg);
}
.flip:hover .flip__back {
  transform: rotateY(360deg);
}
